import React from 'react';
import styled from 'styled-components';
// import { FiSettings } from 'react-icons/fi';
import Logo from '../graphics/Logo';
import NextShuffle from '../graphics/NextShuffle';
import SkipMove from '../graphics/SkipMove';
import UndoMove from '../graphics/UndoMove';

const HeaderWrapper = styled.div`
    /* display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 100%;
    height: 2rem;
    & > *:nth-child(0) {
        justify-self: start;
        grid-column: 1;
    }
    & > *:nth-child(0) svg {
        height: 2rem;
    }
    & > *:nth-child(1) {
        justify-self: center;
        grid-column: 2;
    }
    & > *:nth-child(2) {
        justify-self: end;
        grid-column: 3;
    } */

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 3.5rem;
    & > * {
        flex-basis: 0;
        flex-grow: 1;
    }
    h2 {
        text-align: center;
    }
    #logo {
        height: 100%;
        border-radius: 5px;
        border: 1px solid #955C07;
    }
    #next-shuffle, #skip-move, #undo-move {
        cursor: pointer;
    }
`

const SvgWrapper = styled.div`
    margin: auto 0.5rem;
    padding: 0.2rem;
    display: flex;
    justify-content: ${props => 
        props.align === "left" 
            ? "flex-start" 
            : props.align === "right" 
                ? "flex-end" 
                : "center"};
    align-items: center;

    & > * {
        color: ${({ theme }) => theme.text};
    }
`

export default function Header({ reshuffle, skipMove, undoMove }) {
    return (
        <HeaderWrapper>
            <SvgWrapper align="left">
                <Logo size="2.5rem" />
            </SvgWrapper>
            <h2>Ã˜rkenvandring</h2>
            <SvgWrapper align="right">
                <UndoMove size="2.5rem" undoMove={undoMove} />
                <SkipMove size="2.5rem" skipMove={skipMove} />
                <NextShuffle size="2.5rem" reshuffle={reshuffle} />
            </SvgWrapper>
        </HeaderWrapper>
    )
}